@import "../../scss/variables";
@import "../../scss/mixins";

.Navbar {
  background-color: $cardNavBgColor;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 1rem;
  position: fixed;
  z-index: 4;
  width: 100%;
  top: 0;

  .links {
    display: flex;
    font-size: 1.5rem;
    justify-content: flex-end;
    gap: 2%;
    /* margin-left: 5vh; */

    a {
      color: $secondaryColor;
      text-decoration: none;
      text-transform: uppercase;
      border-radius: 5px;
      padding: 0.5rem;

      &:hover {
        background-color: $secondaryColor;
        color: $cardNavBgColor;
      }
    }
  }
  img {
    width: 100px;
    height: 100px;
  }
}

@include media-xsm{
    .links{
        flex-direction: column;
    }
}
@include media-lg{
    .links{
        flex-direction: row;
    }
}